
import React, { Component } from 'react';
import '../N26Signup.css';
import 'tachyons';
import _ from 'lodash';
import { connect } from 'react-redux';
import { push } from 'react-router-redux';

import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import CircularProgress from '@material-ui/core/CircularProgress';

import agent from '../../../agent';
import loader from '../../img/loader.gif';

class MobileNumber extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userDetails: {},
      otp: '',
      error: '',
      loading: false,
      hideNav: false,
    };
    this.verifyOtp = this.verifyOtp.bind(this);
  }

  componentWillMount() {
    console.log(this.props);
    this.setState({ userDetails: this.props.details.userDetails });
  }

  componentDidMount() {
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
  }

  verifyOtp(e) {
    this.setState({ loading: true });

    e.preventDefault();
    console.log(this.state.userDetails);
    const data = {
      phone: this.state.userDetails.mobile,
      otp: this.state.otp,
    };
    console.log(data);
    agent.Auth.n26OtpVerify(data).then((res) => {
      this.setState({ loading: false });

      console.log(res);
      this.setState({ error: '' });
      this.props.details.updateSequence(this.state.userDetails, this.props.details.index);
    }).catch((err) => {
      this.setState({ loading: false });
      if (err.response) {
        console.log(err.response);
        this.setState({ error: err.response.data.data });
        this.props.details.updateSequence(this.state.userDetails, this.props.details.index);
      }
    });
  }


  resize() {
    this.setState({ hideNav: window.innerWidth <= 760 });
  }

  render() {
    const {
      mobile, country_code,
    } = this.state.userDetails;
    return (
      <div className="outer-div">
        <div className="hv hw hx">
          {
              this.state.hideNav ? null
                : <h2 tabIndex="-1" className="b">OTP Verification</h2>
        }
          <p className="cd ic">{`We have sent you an OTP on mobile number ${country_code} ${mobile} .`}</p>
          <div className=" w-60 ">
            <form onSubmit={this.verifyOtp}>
              <div>
                <div className="input-label-div">
                  <input
                    type="text"
                    name="phone-number"
                    className="inp"
                    required
                    value={this.state.otp}
                    onChange={(e) => this.setState({ otp: e.target.value })}
                  />
                  <label className="inp-label">
                        Enter OTP
                  </label>
                </div>
              </div>
              {
                    this.state.error ? (
                      <strong className="flex" style={{ color: 'red' }}>
                        {` Error : ${this.state.error} `}
                      </strong>
                    ) : <strong className="flex" />
                }
              <div className=" flex">
                <button type="button" className="back-btn" onClick={() => this.props.details.backSequence(this.props.details.index - 1)}>
                  <KeyboardBackspaceIcon />
                  <p aria-hidden="true">Back</p>
                </button>
                <button type="submit" className="continue-btn  pointer  " onClick={this.verifyOtp}>
                  {' '}
                  {this.state.loading ? (
                   <CircularProgress style={{color:'white',width:'20px',height:'20px',marginRight:'10px'}}/>
                  ) : null}
Verify
                </button>
              </div>
            </form>
          </div>

        </div>


      </div>
    );
  }
}

// console.log("in the admin mstp func", state)
const mapStateToProps = (state) => ({

});
const mapDispatchToProps = (dispatch) => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(MobileNumber);
