import React from 'react';
import {push} from 'react-router-redux'

import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import {store} from '../../store'
import './SideDrawer.css'

const useStyles = makeStyles({
  list: {
    width: '100%',
  },
  fullList: {
    width: 'auto',
  },
});

let pages = [{name:'Explore More',url:'/n26-sign-up'}, {name:'OverView',url:'/n26-sign-up'}, {name:'Pages',url:'/n26-sign-up'},{name:'Sections',url:'/n26-sign-up'},{name:'Docs',url:'/n26-sign-up'}]
export default function SwipeableTemporaryDrawer(props) {
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    right: false,
  });

  const toggleDrawer = (side, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [side]: open });
  };

  const sideList = (side) => (
    <div
      className='side-drawer-div'
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
      style={{width:window.innerWidth}}
    >
      <List>
        {pages.map((p, index) => (

          <ListItem button style={{ color: 'white' }} key={p.name} onClick={()=>store.dispatch(push(p.url))}>
            <ListItemText primary={p.name} />
          </ListItem>
        ))}
      </List>
    </div>
  );


  return (
    <Drawer open={props.open} onClose={() => props.toggleDrawer('right', false)} anchor="right" >
      {sideList('right')}
    </Drawer>
  );
}
